.facilities {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  height: 100vh;
  padding: 50px 40px;
}
.facilities > :first-child {
  margin-right: 40px;
  max-width: 50%;
}
.facilities h1 {
  text-align: center;
  text-transform: uppercase;
}
.facilities p {
  text-align: justify;
}
.facilities__about {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 40px;
}
.facilities__about > * {
  margin: 20px;
}
.facilities__about > h1 {
  text-align: center;
  text-transform: uppercase;
}
.facilities__about > p {
  font-size: 1.2rem;
  max-width: 600px;
  text-align: justify;
}
.facilities__buttons {
  display: flex;
  flex-direction: column;
}
.facilities__buttons > a {
  text-decoration: none;
}
.facilities__buttons Button {
  width: 100%;
}
.facilities__buttons > :not(:last-child) {
  margin-bottom: 20px;
}
.facilities__equipment, {
  background-color: #ffffff;
  background-image: url('../assets/textures/connected.png');
  color: var(--text-dark);
}
.facilities__equipment h2 {
  border-bottom: 2px solid var(--color-complement-3);
  color: var(--color-complement-3);
}
.facilities__equipment h3,
.facilities__equipment h4 {
  color: var(--color-complement-3);
}
.facilities__equipment img {
  height: 40px;
}
.facilities__equipment img:not(:first-of-type) {
  margin-top: 30px;
}
.facilities__equipment svg {
  fill: var(--color-complement-3);
}
.facilities__landing {
  align-items: center;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: center;
  min-height: 100vh;
  padding: 50px 40px;
}
.facilities__links {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
}
.facilities__links > a {
  flex-basis: 0;
  margin: 10px;
  min-width: 150px;
  text-decoration: none;
}
.facilities__main-about h1,
.facilities__main-about p {
  border-radius: 4px;
}
.facilities__main-about h1 {
  background-color: rgba(245, 245, 245, 0.85);
  padding: 10px;
}
.facilities__main-about p {
  background-color: rgba(245, 245, 245, 0.85);
  font-size: 1.2rem;
  max-width: 600px;
  padding: 20px;
  text-align: justify;
}
.facilities__main-landing {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
  min-height: 100vh;
  padding: 50px 40px;
}
.facilities__pricing {
  background: var(--color-secondary-1-1);
  background: linear-gradient(to right, #aeccea, #c2d9ef);
}
.facilities__pricing h2 {
  border-bottom: 2px solid var(--text-dark);
  color: var(--text-dark);
}
.facilities__pricing h3 span {
  border-bottom: 2px dotted var(--text-dark);
  box-shadow: none, inset 0 1px 0 0 var(--text-dark), none, none;
}
.facilities__pricing svg {
  fill: var(--text-dark);
}
.facilities__table svg {
  fill: var(--color-secondary-1-4);
}
.facilities__table {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0 auto;
}
.facilities__table thead {
  background-color: var(--color-secondary-1-2-5);
  color: var(--text-light);
}
.facilities__table thead td {
  text-align: center;
}
.facilities__table td,
.facilities__table th {
  padding: 4px 15px;
}
.facilities__table tbody tr:nth-child(2n+1) {
  background-color: #EEEEEE;
}
.facilities__table tbody tr:nth-child(2n+2) {
  background-color: #E0E0E0;
}
.facilities__table tfoot {
  border-top: 1px solid var(--text-dark);
}
.facilities__table .td-center {
  text-align: center;
}
.facilities__table_extra-wide,
.facilities__table_wide {
  overflow-x: auto;
}
.facilities__table_extra-wide table {
  min-width: 600px;
}
.facilities__table_wide table {
  min-width: 500px;
}
.facilities__other-links {
  background-color: #ffffff;
  background-image: url('../assets/textures/light-wool.png');
}
.facilities__other-links h2 {
  border-bottom: 2px solid var(--text-dark);
  color: var(--text-dark);
}
.facilities__other-links h3 {
  color: var(--text-dark);
}
.facilities__other-links svg {
  fill: var(--text-dark);
}
.facilities__other-links ul {
  list-style: none;
}
.facilities__protocols,
.facilities__gallery {
  background-color: #ffffff;
  background-image: url('../assets/textures/light-wool.png');
}
.facilities__protocols h2,
.facilities__gallery h2 {
  border-bottom: 2px solid var(--color-secondary-1-4);
  color: var(--color-secondary-1-4);
}
.facilities__protocols h3 {
  color: var(--color-secondary-1-4);
}
.facilities__protocols svg,
.facilities__gallery svg {
  fill: var(--color-secondary-1-4);
}
.facilities__protocols a,
.facilities__other-links a {
  text-decoration: none;
  transition: color 0.3s ease;
}
.facilities__protocols a svg,
.facilities__other-links a svg {
  transition: fill 0.3s ease;
}
.facilities__protocols a:link,
.facilities__protocols a:visited,
.facilities__other-links a:link,
.facilities__other-links a:visited {
  color: var(--text-dark);
}
.facilities__protocols a:link svg,
.facilities__protocols a:visited svg,
.facilities__other-links a:link svg,
.facilities__other-links a:visited svg {
  fill: var(--text-dark);
}
.facilities__protocols a:focus,
.facilities__protocols a:hover,
.facilities__protocols a:active,
.facilities__other-links a:focus,
.facilities__other-links a:hover,
.facilities__other-links a:active {
  color: var(--color-primary-2);
}
.facilities__protocols a:focus svg,
.facilities__protocols a:hover svg,
.facilities__protocols a:active svg,
.facilities__other-links a:focus svg,
.facilities__other-links a:hover svg,
.facilities__other-links a:active svg,  {
  fill: var(--color-primary-2);
}
.facilities__protocols li,
.facilities__other-links li {
  margin: 5px 0;
}
.facilities__protocols svg,
.facilities__other-links svg {
  height: 16px;
  margin-right: 5px;
}
.facilities__protocols_plain-list {
  list-style: none;
  padding-left: 0;
}
.facilities__section {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 60px 40px;
}
.facilities__section h2 {
  align-items: center;
  align-self: flex-start;
  display: flex;
  margin-left: 5%;
  margin-bottom: 50px;
  padding-bottom: 5px;
  text-transform: uppercase;
  vertical-align: middle;
  width: 50%;
}
.facilities__section h2 svg {
  height: 25px;
  margin-right: 10px;
}
.facilities__section p {
  text-align: justify;
}
.facilities__section-inner {
  max-width: 950px;
  width: 100%;
}
.facilities__services {
  background-color: var(--light-background);
  color: var(--text-dark);
}
.facilities__services h2 {
  border-bottom: 2px solid var(--color-secondary-1-4);
  color: var(--color-secondary-1-4);
}
.facilities__services h3,
.facilities__services h4 {
  color: var(--color-secondary-1-4);
}
.facilities__services h3 span {
  border-bottom: 2px dotted var(--text-dark);
  box-shadow: none, inset 0 1px 0 0 var(--text-dark), none, none;
}
.facilities__services svg {
  fill: var(--color-secondary-1-4);
}
.facilities__tools {
  background-color: var(--light-background);
  color: var(--text-dark);
}
.facilities__tools h2 {
  border-bottom: 2px solid var(--color-secondary-1-4);
  color: var(--color-secondary-1-4);
}
.facilities__tools h3 {
  color: var(--color-secondary-1-4);
}
.facilities__tools h3 span {
  border-bottom: 2px dotted var(--text-dark);
  box-shadow: none, inset 0 1px 0 0 var(--text-dark), none, none;
}
.facilities__tools svg {
  fill: var(--color-secondary-1-4);
}
.facilities__tools ul {
  list-style: none;
}
.facilities__tools a {
  text-decoration: none;
  transition: color 0.3s ease;
}
.facilities__tools a svg {
  transition: fill 0.3s ease;
}
.facilities__tools a:link,
.facilities__tools a:visited {
  color: var(--color-secondary-1-4);
}
.facilities__tools a:link svg,
.facilities__tools a:visited svg {
  fill: var(--color-secondary-1-4);
}
.facilities__tools a:focus,
.facilities__tools a:hover,
.facilities__tools a:active {
  color: var(--color-complement-3);
}
.facilities__tools a:focus svg,
.facilities__tools a:hover svg,
.facilities__tools a:active svg {
  fill: var(--color-complement-3);
}
.facilities__tools svg {
  height: 16px;
  margin-right: 5px;
}
@media (max-width: 749px) {
  .facilities {
    flex-direction: column;
    height: auto;
    min-height: 100vh;
  }
  .facilities > :first-child {
    margin-bottom: 20px;
    margin-right: 0;
    max-width: 95%;
  }
  .facilities__section h2 {
    border: none;
    justify-content: center;
    margin-left: 0;
    width: 100%;
  }
  .facilities__table_wide {
    max-width: 100%;
    overflow-x: auto;
  }
}
@media (max-width: 449px) {
  .facilities strong {
    font-size: 0.9em;
  }
  .facilities__about > p {
    font-size: 1rem;
  }
  .facilities__landing {
    padding: 50px 20px 20px 20px;
  }
  .facilities__pricing table {
    font-size: 0.8em;
  }
  .facilities__protocols table {
    font-size: 0.8em;
  }
  .facilities__section {
    padding: 60px 20px;
  }
}
@media (max-width: 374px) {
  .facilities__protocols table {
    font-size: 0.7em;
  }
}
@media print {
  .facilities {
    padding: 0;
  }
  .facilities__buttons {
    display: none;
  }
  .facilities__links {
    display: none;
  }
  .facilities__section {
    padding: 0;
  }
  .facilities__section h2 {
    margin-bottom: 10px;
  }
  .facilities__gallery {
    display: none;
  }
  .facilities__equipment,
  .facilities__gallery,
  .facilities__links,
  .facilities__pricing,
  .facilities__protocols,
  .facilities__services,
  .facilities__tools {
    background-color: none;
  }
  .facilities__equipment h2,
  .facilities__links h2,
  .facilities__pricing h2,
  .facilities__protocols h2,
  .facilities__services h2,
  .facilities__tools h2 {
    color: black;
  }
  .facilities__equipment h3,
  .facilities__links h3,
  .facilities__pricing h3,
  .facilities__protocols h3,
  .facilities__services h3,
  .facilities__tools h3,
  .facilities__equipment h4,
  .facilities__gallery h4,
  .facilities__links h4,
  .facilities__pricing h4,
  .facilities__protocols h5,
  .facilities__services h4,
  .facilities__tools h4 {
    color: black;
  }
  .facilities__equipment h3 span,
  .facilities__links h3 span,
  .facilities__pricing h3 span,
  .facilities__protocols h3 span,
  .facilities__services h3 span,
  .facilities__tools h3 span {
    border-bottom: 2px dotted black;
    box-shadow: none, inset 0 1px 0 0 var(--text-dark), none, none;
  }
  .facilities__equipment svg,
  .facilities__links svg,
  .facilities__pricing svg,
  .facilities__protocols svg,
  .facilities__services svg,
  .facilities__tools svg {
    fill: black;
  }
  .facilities__protocols_plain-list {
    font-family: sans-serif;
  }
  .facilities__table thead {
    background-color: none;
    color: black;
  }
  .facilities__table th {
    border-bottom: 1px solid black;
  }
  .facilities__table_extra-wide,
  .facilities__table_wide {
    font-size: 12px;
  }
}

@media (min-width: 1801px) {
  .facilities__main-landing {
    background-image: url('./assets/facilities-5000x3333.jpg');
  }
}
@media (max-width: 1800px) {
  .facilities__main-landing {
    background-image: url('./assets/facilities-1800x1440.jpg');
  }
}
@media (max-width: 1200px) {
  .facilities__main-landing {
    background-image: url('./assets/facilities-1200x900.jpg');
  }
}
@media (max-width: 900px) {
  .facilities__main-landing {
    background-image: url('./assets/facilities-900x1600.jpg');
  }
}
@media (max-width: 600px) {
  .facilities__main-landing {
    background-image: url('./assets/facilities-600x1000.jpg');
  }
}